/* Global style of tags */
html {
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  min-height: 100%;
  font-family: "Fira Sans", Arial, sans-serif;
  font-weight: 400;
  font-size: 16px;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

.visually-hidden:not(:focus):not(:active) {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

/* Blocks styles */
.container {
  margin: 0 auto;
  width: 270px;
  height: auto;
}
@media (min-width: 768px) {
  .container {
    width: 708px;
  }
}
@media (min-width: 1440px) {
  .container {
    width: 1196px;
  }
}
.container--menu {
  width: 270px;
}
@media (min-width: 768px) {
  .container--menu {
    width: 708px;
  }
}
@media (min-width: 1440px) {
  .container--menu {
    width: 1400px;
  }
}

.page-header {
  background-color: #f7a254;
}

.page-header__menu {
  display: flex;
  flex-flow: wrap;
  justify-content: space-between;
  align-items: center;
  padding-top: 25px;
  padding-bottom: 25px;
}

.logo {
  display: flex;
  align-items: center;
}

.logo__svg {
  margin-right: 10px;
}

.logo__img {
  width: 150px;
  height: 20px;
}

.menu {
  margin: 0;
  padding: 0;
  list-style: none;
  display: none;
  flex-flow: wrap;
}
@media (min-width: 1440px) {
  .menu {
    display: flex;
  }
}
.menu--active {
  display: flex;
}

.menu__item:not(:last-child) {
  margin-right: 40px;
}

.menu__link {
  display: block;
  padding-top: 5px;
  padding-right: 15px;
  padding-bottom: 5px;
  padding-left: 15px;
  font-size: 18px;
  font-weight: 500;
  color: #fff;
  border-radius: 10px;
}
@media (min-width: 1440px) {
  .menu__link {
    font-size: 18px;
  }
}
.menu__link:hover {
  background-color: #6aa6ff;
  cursor: pointer;
}
.menu__link--active {
  background-color: #6aa6ff;
}

.menu__button {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  width: 38px;
  height: 38px;
  padding-top: 10px;
  padding-bottom: 10px;
  background: none;
  border: none;
  border-radius: 10px;
}
@media (min-width: 1440px) {
  .menu__button {
    display: none;
  }
}
.menu__button:hover {
  background-color: #6aa6ff;
  cursor: pointer;
}

.menu__button-line {
  width: 24px;
  height: 2px;
  background-color: #ffffff;
}

.header {
  padding: 0;
  margin: 0;
}
.header--1 {
  font-size: 18px;
}
.header--2 {
  font-size: 14px;
  font-weight: 400;
}

.devices__header-1 {
  margin-bottom: 20px;
}

.devices__header-2 {
  margin-bottom: 10px;
}
@media (min-width: 768px) {
  .devices__header-2 {
    font-size: 18px;
  }
}
@media (min-width: 1440px) {
  .devices__header-2 {
    font-size: 24px;
  }
}

.devices-list {
  margin: 0;
  padding: 0;
  list-style: none;
}
@media (min-width: 768px) {
  .devices-list {
    display: flex;
    flex-direction: row;
  }
}

.devices-list__item {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: center;
  align-items: center;
  max-width: 270px;
  padding-top: 15px;
  padding-right: 15px;
  padding-bottom: 15px;
  padding-left: 15px;
  background-color: #ffffff;
  border-radius: 10px;
}
@media (min-width: 768px) {
  .devices-list__item {
    flex: 1 1 auto;
  }
}
.devices-list__item:not(:last-child) {
  margin-bottom: 5px;
}
@media (min-width: 768px) {
  .devices-list__item:not(:last-child) {
    margin: 0;
    margin-right: 10px;
  }
}
.devices-list__item:hover {
  color: #fff;
  background-color: #6aa6ff;
  cursor: pointer;
}
.devices-list__item:hover .devices-list__icon-path {
  fill: #ffffff;
}

.devices-list__status {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-right: 15px;
}

.devices-list__icon {
  margin-bottom: 10px;
  fill: #000000;
}

.devices-list__icon-path {
  fill: #000000;
}

.devices-list__status-text {
  position: relative;
}
.devices-list__status-text--online::after {
  content: "";
  position: absolute;
  top: 50%;
  right: 0;
  width: 8px;
  height: 8px;
  background-color: #00ff47;
  border-radius: 50%;
  transform: translateY(-50%);
}
.devices-list__status-text--offline::after {
  content: "";
  position: absolute;
  top: 50%;
  right: 0;
  width: 8px;
  height: 8px;
  background-color: #ff0000;
  border-radius: 50%;
  transform: translateY(-50%);
}

.devices-list__name {
  font-size: 16px;
}

/* Active state item of devices-list */
.devices-list__item--active {
  background-color: #6aa6ff;
}

.devices-list__item--active .devices-list__icon-path {
  fill: #ffffff;
}

.devices-list__item--active .devices-list__status-text {
  color: #ffffff;
}

.devices-list__item--active .devices-list__name {
  color: #ffffff;
}

.header-section {
  padding-top: 20px;
}

.header-section__header {
  margin-bottom: 25px;
}
@media (min-width: 768px) {
  .header-section__header {
    font-size: 24px;
  }
}
@media (min-width: 1440px) {
  .header-section__header {
    font-size: 32px;
  }
}

.page-content {
  padding-bottom: 20px;
}

.page-content__devices {
  margin-bottom: 15px;
}

.page-content__carriers {
  margin-bottom: 15px;
}

.carriers__header-2 {
  margin-bottom: 10px;
}
@media (min-width: 768px) {
  .carriers__header-2 {
    font-size: 18px;
  }
}
@media (min-width: 1440px) {
  .carriers__header-2 {
    font-size: 24px;
  }
}

.carriers-list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.carriers-list__item {
  display: flex;
  flex-direction: column;
  padding-top: 10px;
  padding-right: 10px;
  padding-bottom: 10px;
  padding-left: 10px;
  border-radius: 10px;
}
.carriers-list__item:not(:last-child) {
  margin-right: 5px;
}
.carriers-list__item:hover {
  background-color: #6aa6ff;
  color: #ffffff;
  cursor: pointer;
}
.carriers-list__item:hover .carriers-list__icon-path {
  fill: #ffffff;
}

.carriers-list__name {
  display: flex;
  flex-direction: column;
}

.carriers-list__name-text {
  font-size: 16px;
}

.carriers-list__icon {
  margin-bottom: 10px;
}

.carriers-list__icon-path {
  fill: #000000;
}

.carriers-list__status {
  position: relative;
  font-size: 14px;
}
.carriers-list__status--online::after {
  content: "";
  position: absolute;
  top: 50%;
  right: 0;
  width: 8px;
  height: 8px;
  background-color: #00ff47;
  border-radius: 50%;
  transform: translateY(-50%);
}
.carriers-list__status--offline::after {
  content: "";
  position: absolute;
  top: 50%;
  right: 0;
  width: 8px;
  height: 8px;
  background-color: #ff0000;
  border-radius: 50%;
  transform: translateY(-50%);
}

/* Active state item of devices-list */
.carriers-list__item--active {
  background-color: #6aa6ff;
}

.carriers-list__item--active .carriers-list__icon-path {
  fill: #ffffff;
}

.carriers-list__item--active .carriers-list__name-text {
  color: #ffffff;
}

.carriers-list__item--active .carriers-list__status {
  color: #ffffff;
}

.files__header-2 {
  margin-bottom: 10px;
}
@media (min-width: 768px) {
  .files__header-2 {
    font-size: 18px;
  }
}
@media (min-width: 1440px) {
  .files__header-2 {
    font-size: 24px;
  }
}

.files__table {
  width: 100%;
  border-collapse: collapse;
}

.files__th {
  padding-top: 12px;
  padding-bottom: 12px;
  padding-left: 5px;
  color: #ffffff;
  text-align: left;
  font-size: 12px;
  background-color: #6aa6ff;
}
@media (min-width: 768px) {
  .files__th {
    font-size: 16px;
  }
}

.files__td {
  padding-top: 5px;
  padding-bottom: 5px;
  padding-left: 5px;
  min-width: 25%;
  font-size: 12px;
}
@media (min-width: 768px) {
  .files__td {
    font-size: 16px;
  }
}
.files__td--download {
  text-decoration: underline;
  cursor: pointer;
}

.page-footer {
  background-color: #f7a254;
}

.page-footer__inner {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding-top: 30px;
  padding-bottom: 30px;
}
@media (min-width: 768px) {
  .page-footer__inner {
    flex-direction: row;
    justify-content: space-between;
    padding-top: 30px;
    padding-bottom: 30px;
  }
}

.page-footer__text {
  color: #fff;
}

.contacts-list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  align-items: center;
}

.contacts-list__item:not(:last-child) {
  margin-right: 10px;
}